.link-back-to-articles {
  font-family: 'Mukta Malar';
  font-weight: 100;
  font-size: 1.5rem;
  margin: 3rem;
  padding-bottom: 2rem;
}
.article-wrapper {
  max-width: 100%;
  display: -ms-flexbox;
  display: flex;
  padding-right: 3rem;
  padding-left: 3rem;
}
.article-content-wrapper { max-width: 48rem; }
.article-title { max-width: 30rem; margin-top: -.2rem; padding-right: 2rem; }
.title { text-align: right; }
.thumbnail { width: 100%; max-width: 30rem; float: right; }
h1 {
  font-family: 'Mukta Malar';
  font-size: 6rem;
  line-height: 6.3rem;
  margin-top: 0rem;
}
h2 {
  font-family: 'Mukta Malar';
  font-size: 2rem;
  margin-top: 3rem;
  margin-bottom: .5rem;
}
p {
  max-width: 100vw;
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  font-size: 1.2rem;
  padding: .2rem 0rem;
}
img {
  width: 100%;
  padding-top: .4rem;
}
a { color: pink; word-break: break-all;}

@media screen and (max-width: 1130px) {
  .article-wrapper { display: inline-block; padding-right: 0rem; }
  .title { text-align: left; }
  .thumbnail { float: none; display: block; padding-bottom: 3rem; }
}

@media screen and (max-width: 600px) {
  .link-back-to-articles { margin: 1rem; }
  .article-wrapper { padding: 0rem 1rem; }
  h1 { font-size: 4rem; line-height: 4rem; }
  .thumbnail { padding-bottom: 0rem; }
}



